export function formatDate(dateString) {
  const date = new Date(dateString);
  let dd = date.getDate() + 1;
  let mm = date.getMonth() + 1;

  const yyyy = date.getFullYear();
  if (dd < 10) {
    dd = '0' + dd;
  }
  if (mm < 10) {
    mm = '0' + mm;
  }
  return `${mm}/${dd}/${yyyy}`;
}

export function timeAgo(date) {
  const NOW = new Date();
  const times = [['second', 1], ['minute', 60], ['hour', 3600], ['day', 86400], ['week', 604800], ['month', 2592000], ['year', 31536000]];
  var diff = Math.round((NOW - date) / 1000);
  for (var t = 0; t < times.length; t++) {
    if (diff < times[t][1]) {
      if (t == 0) {
        return 'Just now';
      } else {
        diff = Math.round(diff / times[t - 1][1]);
        return diff + ' ' + times[t - 1][0] + (diff == 1 ? ' ago' : 's ago');
      }
    }
  }
}

export function formatTimeAgo(timeValue) {
  timeValue = (new Date() - new Date(timeValue)) / 1000;
  const years = Math.floor(timeValue / 31536000);
  const months = Math.floor(timeValue / 2592000);
  const days = Math.floor(timeValue / 86400);
  const hours = Math.floor(timeValue / 3600);
  const minutes = Math.floor(timeValue / 60);
  let ret = '';

  if (years > 0) {
    return `${years} year${years === 1 ? 's' : ''} ago`;
  }

  if (months > 0) {
    if (ret !== '') {
      ret += ' ';
    }
    return months + ' months ago';
  }

  if (days > 0) {
    if (ret !== '') {
      ret += ' ';
    }
    return days + ' days ago';
  }

  if (hours > 0) {
    if (ret !== '') {
      ret += ' ';
    }
    return hours + ' hours ago';
  }

  if (minutes > 0) {
    if (ret !== '') {
      ret += ' ';
    }
    return minutes + ' minutes ago';
  }

  return ret + ' ago';
}
